@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

:root {
  --color-primary: #ae3ec9;
  --color-primary-dark: #9c36b5;
  --color-primary-light: #f3d9fa;
  --color-black: #495057;
  --color-white: #f8f9fa;
  --color-grey-light-1: #e9ecef;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
  font-weight: 400;
  color: var(--color-black);

  background-image: linear-gradient(
    120deg,
    rgba(174, 62, 201, 0.85),
    rgba(255, 115, 0, 0.85)
  );
  background-repeat: no-repeat;
  height: 100vh;
}

.app {
  display: grid;
  grid-template-columns:
    [full-start left-start] minmax(6rem, 1fr) [left-end center-start] repeat(
      10,
      [col-start] minmax(min-content, 8rem) [col-end]
    )
    [center-end right-start] minmax(6rem, 1fr)
    [right-end full-end];
}

.container {
  background-color: var(--color-white);
  grid-column: col-start 2 / col-start -1;
  margin: 7rem 0;
  padding: 3rem 7rem;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.07);

  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.container div:nth-child(3) {
  margin-bottom: 3rem;
}

.container > div {
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: space-between;
}

.container div:nth-child(4),
.container div:last-child {
  justify-content: center;
}

.output {
  font-size: 2.4rem;
  margin-bottom: 3rem;
}

input,
select,
button {
  font-family: inherit;
  border: none;
}

input,
select {
  padding: 1rem 3rem;
  border-radius: 100px;
  width: 17rem;
  background-color: var(--color-grey-light-1);
  text-align: center;
}

input:focus,
select:focus {
  outline: 3px solid var(--color-primary);
  background-color: var(--color-primary-light);
}

.btn {
  cursor: pointer;
  padding: 1rem 2rem;
  text-transform: uppercase;
  background-color: var(--color-primary);
  color: var(--color-white);
  font-weight: 500;
  letter-spacing: 1px;
  transition: all 0.2s;
}

.btn:hover,
.btn:active {
  background-color: var(--color-primary-dark);
}

.btn:focus {
  outline: 3px solid var(--color-primary);
  background-color: var(--color-primary-light);
  color: var(--color-black);
}
